import React, { useState, useEffect } from "react"
import Aos from 'aos'
import 'aos/dist/aos.css'
import Button from "@material-ui/core/Button"

import NominatedMovie from "../components/NominatedMovie"

export default function FlixPage(){
  let array = JSON.parse(localStorage.getItem("nominated"))
  console.log("arrayyyy: " + array)
  const [nominationsArray, setNominationsArray]= useState(generateArray)

  function removeNomination(data){
    let temp = []
    for (let i = 0; i < array.length; i++){
        if (array[i].title !== data.title){
          temp.push(array[i])
        }
    }
    array = temp
    localStorage.setItem("nominated", JSON.stringify(array))
    setNominationsArray(generateArray)
  }

  function generateArray(){
    let temp = []
    for(let i = 0; i < 5; i++){
      let size = ""
      if(i === 0){
        size = "3rem"
      }
      else if(i === 1){
        size = "2.75rem"
      }
      else{
        size = "2.5rem"
      }

      temp.push(
        <li className="empty-flix" style={{fontSize: size,}} data-aos="fade-up" data-aos-once="true" data-aos-delay={i*100}>
          {array.length > i ?
          <>
            <Button onClick={()=>{removeNomination(array[i])}} className="remove-movie" color="secondary" variant="outlined" size="small" 
              style={{zIndex: "5", color: "white", fontWeight: "bold", margin: "3%", position: "absolute", top: "0", right: "0", width: "1.5rem", height: "1.5rem"}}>
                X
            </Button>
            <NominatedMovie 
              key={array[i].title+array[i].year+String(Math.random())}
              wallpaper={array[i].wallpaper}
              title={array[i].title}
              year={array[i].year}
              plot={array[i].plot}
              rating={array[i].rating}
              runtime={array[i].Runtime}
              rated={array[i].rated}
          />
          </> : ""}
          <div className="home-page-background"></div>
        </li>
      )
    }
    return temp
  }

  console.log(nominationsArray)
  
  useEffect(() => {
    Aos.init({duration: 1500})
  }, [])
    return(
      <div className="flix-page">
        <h1 className="flix-page-title" data-aos="fade-up" data-aos-once="true" >Nominated Flix</h1>
        <div className="nominated-flix-container"  data-aos="fade-up" data-aos-once="true" data-aos-delay="250">
          <ol className="nominated-list" style={{color: array.length > 0 ? "white" : "rgb(70, 70, 70)"}}>
            {nominationsArray}
          </ol>
          <p className="empty-message">
            Nominated movies will be shown here!<br/>
            See the search tab to find movies to nominate
          </p>
        </div>
      </div>
    )
}