import React, {useState, useEffect} from "react"
import Aos from 'aos'
import 'aos/dist/aos.css'

import SearchResults from "./SearchResults"
import Movie from "./Movie"

function Search(){
    const APIKey = "3918a29a";
    let searchArray = [];
    let [searchValue, setSearchValue] = useState("");
    let [searchResults, setSearchResults] = useState([]);

    function useInput(){
        const input = 
        <input type="text" className="search-bar" 
        onChange={e => setSearchValue(e.target.value)} 
        onKeyUp={function(e){
            if(searchValue === "" && e.key === "Backspace"){
                setSearchResults([])
            }
            if(e.key === "Enter"){
                searchArray = []
                setSearchResults([])
                setTimeout(function(){updateSearch()}, 1000)

            }
            searchArray = []
            getRequest()

        }}/>
        return [input]
    }

    function updateSearch(){
        console.log("SEARCH ARRAY")
        console.log(searchArray)
        if(searchValue === ""){
            return
        }
        if(searchArray.length < 1){
            setSearchResults([<p className="no-results">No results found</p>])
            return;
        }

        searchArray.forEach(result => {
            if (result.Poster !== "N/A" && result.Poster !== undefined ){
                //check local storage if movie has already been nominated
                let temp = JSON.parse(localStorage.getItem("nominated"))
                let bool = false
                //console.log("TEMPPP: " + temp)
                for(let i = 0; i < temp.length; i++){
                    if (temp[i].title === result.Title){
                        bool = true
                        break
                    }
                }
                searchResults.push(
                    <Movie
                        key={result.Title+result.Year+String(Math.random())}
                        wallpaper={result.Poster}
                        title={result.Title}
                        year={"("+result.Year+")"}
                        plot={result.Plot}
                        rating={result.imdbRating}
                        rated={result.Rated}
                        runtime={result.Runtime}
                        nom={bool}
                    />
                );
            }
            //console.log(searchResults);
            setSearchResults([...searchResults], ()=> {console.log("error")});
        });
    }

    useEffect(() => {
        Aos.init({duration: 1500})
      }, [])
    async function getRequest(){
        if(searchValue.length < 1){
            return
        }
        //console.log(searchValue)
        let temp = [];
        let data = []
        for(let i = 1; i < 3; i++){
            data = await fetch(`https://www.omdbapi.com/?s=${searchValue}&i=tt3896198&apikey=${APIKey}&page=${i}`)
            .then(response => response.json())
            .then(data => {
                let temp2 = data.Search
                return temp2
            })
            if (data != undefined){
                temp.push(...data)
            }
            console.log(data)
        }
            temp.forEach(result => {
                fetch(`https://www.omdbapi.com/?t=${String(result.Title)}&i=tt3896198&apikey=${APIKey}`)
                .then(response => response.json())
                .then(data => {
                    console.log("--DATA--")
                    console.log(data)
                    searchArray.push(data)
                })
            });
            console.log(searchArray)
    }
    return(
        <React.Fragment>            
            <div className="search-bar-container" data-aos="fade-up" data-aos-once="true">
                <input type="image" className="nav-icon search-icon" src="/resources/img/searchblack.svg"
                    onClick={()=>{
                        console.log("clicked")
                        updateSearch()
                    }}
                />
                {useInput()}
            </div>
            <SearchResults array={searchResults} onSearchChange={setSearchResults}/>
        </React.Fragment>
    )
}

export default Search